import React from 'react';
import classes from './ProductBuy.module.scss';

function ProductBuy({ link }) {
	return (
		<div className={`d-flex justify-content-center ${classes.cont}`}>
			<a rel="noreferrer" className={classes.btnlink} target="_blank" href={link}>
				<button className={classes.btn}>خرید این محصول</button>
			</a>
		</div>
	);
}

export default ProductBuy;
